.meeting-editor label {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.50);
  margin-top: 6px;
}

.meeting-editor input, .meeting-editor textarea {
  margin-top: 10px;
  padding: 5px 10px;
  border: 1px solid #e4e4e4;
  border-radius: 3px;
  color: rgba(0, 0, 0, 0.87);
  resize: none;
}

.meeting-editor input { max-width: 200px; }
.meeting-editor textarea { -ms-flex: 1 1 0%; flex: 1 1 0%; }

.meeting-editor .form-buttons {
  margin-top: 30px;
  text-align: right;
}

.meeting-editor .form-buttons .button {
  margin-left: 10px;
}

.meeting-editor input.invalid {
  border-color: rgba(255, 0, 0, 0.7);
}

.meeting-editor .error-message {
  height: 14px;
  margin-top: 2px;
  color: rgba(255, 0, 0, 0.7);
}



